
#PieWrapper{position:relative
}
#PieChart{position:absolute
}
p{font-size:0.7em
}
svg .test-text{fill:#2C3E50;font-family:'Roboto'
}
svg#pie-chart{display:block;margin:0 auto
}
text.inner-circle{font-weight:400;font-size:12px;text-transform:uppercase
}
.arc{cursor:pointer
}
.arc text{font-weight:300;font-size:12px;color:#fff
}
#extraInfo{position:absolute;top:60%;-ms-transform:translateY(-50%);transform:translateY(-50%);opacity:0;width:4.8em;height:5em;text-align:left;font-size:0.7em;right:-25%
}
#backButton{position:absolute;display:none;width:5em;text-align:center;font-size:0.4em;top:7em;-ms-flex-align:center;padding:1em 1em;cursor:pointer;color:black;border-radius:3em;background-color:#3BCCFF
}
#instructionPie{opacity:0
}
.half-circle-spinner,.half-circle-spinner *{box-sizing:border-box
}
.half-circle-spinner{width:60px;height:60px;border-radius:100%;position:absolute;top:3em;left:0;margin:0 auto;right:0
}
.half-circle-spinner .circle{content:"";position:absolute;width:100%;height:100%;border-radius:100%;border:calc(60px / 10) solid transparent
}
.half-circle-spinner .circle.circle-1{border-top-color:#3BCCFF;animation:half-circle-spinner-animation 1s infinite
}
.half-circle-spinner .circle.circle-2{border-bottom-color:#3BCCFF;animation:half-circle-spinner-animation 1s infinite alternate
}
@keyframes half-circle-spinner-animation{
0%{transform:rotate(0deg)
}
100%{transform:rotate(360deg)
}
}



