
*{box-sizing:border-box
}
.bar-chart-container{margin-top:2em;padding-top:3.5em;padding-bottom:2em;border-top:1px solid black;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;position:relative
}
.bar-chart-container .app__icon{max-width:60px;max-height:60px;border-radius:14px
}
.bar-chart-container .app__name{text-transform:uppercase;font-size:.8em;margin:0;text-align:left;width:calc(50% - 80px);text-decoration:none;color:black;font-weight:700;padding-left:1vw;padding-right:1vw;-ms-flex:1;flex:1
}
.bar-chart-container .app__name:hover{text-decoration:underline;cursor:pointer
}
.app__header{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:0 0.5em;-ms-flex-wrap:wrap;flex-wrap:wrap
}
.bar-chart-container__left,.bar-chart-container__right{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;width:50vw;text-align:left
}
.bar-chart-container__left{-ms-flex-pack:end;justify-content:flex-end
}
.bar-chart-container__left .app__header{display:-ms-flexbox;display:flex;-ms-flex-pack:end;justify-content:flex-end;padding-left:2em
}
.bar-chart-container__left .app__name{text-align:right
}
.bar-chart-container__left .app__description{text-align:right
}
.bar-chart-container__right .app__header{padding-right:2em
}
p{font-size:0.7em
}
.bar-chart-container__svg{width:100%
}
.bar-chart-container__svg .test-text{fill:#2C3E50;font-family:'Roboto'
}
.app__description{margin-top:1em;width:50vw;font-size:.5em
}
.app__chart{margin-top:.5em;-ms-flex-item-align:end;align-self:flex-end
}
#spinner2{display:none;top:1em;margin:auto;right:auto;left:calc(50% - 30px)
}



